}

.konsepsyon__image {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 100;
}
.konsepsyon__image .konsepsyon__image__cancel {
    padding: .5em 1em;
    color: white;
    background-color: red;
    border-radius: 5px;
}
.konsepsyon__image button {
    padding: 1em;
    color: grey;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
}
.konsepsyon__image form {
    padding: 10px;
    color: #259E83;
    background-color: #1D1D26;
    border-radius: 5px;
}
.konsepsyon__image form input {
      border: 1px solid #259E83;
}
.konsepsyon__image form button {
      color: #259E83;
}

.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}

.row__delete {
  position: absolute;
  right: 0;
  top: 0;
  padding: 1em;
  color: red;
  font-size: 1em;
  font-weight: bold;
  cursor: pointer;
}

.row__copy {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 1em;
  color: grey;
  font-size: 1em;
  font-weight: bold;
  cursor: pointer;
}
.row__copy:hover {
