.konsepsyon__rte {
  position: fixed;
  padding: 7px 5px 5px;
  font-family: 'Avenir', sans-serif;
  z-index: 900;
  background-color: #1D1D26;
  border-radius: 5px;
}
.konsepsyon__rte::after {
    content: '';
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    left: 50%;
    bottom: -10px;
    border-style: solid;
    border-width: 10px 5px 0 5px;
    border-color: #1D1D26 transparent transparent transparent;
}
.konsepsyon__rte .sep {
    display: inline-block;
    height: 15px;
    border-left: 1px solid #259E83;
}
.konsepsyon__rte .remove, .konsepsyon__rte .unlink {
    color: red;
}
.konsepsyon__rte .konsepsyon__rte__actions {
    text-align: center;
}
.konsepsyon__rte .vc-compact {
    margin: 0 auto;
}
.konsepsyon__rte button {
    color: white;
    background-color: transparent;
    border: none;
    cursor: pointer;
    -webkit-transition: color .2s;
    transition: color .2s;
}
.konsepsyon__rte button.active {
      color: #259E83;
}
.konsepsyon__rte form {
    margin: .5em 0;
}
.konsepsyon__rte input {
    padding: 5px;
    color: white;
    background-color: transparent;
    border: 1px solid #259E83;
}
.konsepsyon__rte input.large {
      width: 80%;
}
.konsepsyon__rte input.verry {
      width: 254px;
}
.konsepsyon__rte label {
    color: white;
}
.range-slider {
  display: inline-block;
  padding: 0 10px;
  height: 20px;
  width: 130px;
}

.range-slider.disabled {
  opacity: 0.5;
}

.range-slider-inner {
  display: inline-block;
